.photos {
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
  }
  &--wide--right img:first-child {
    width: calc(67% - 10px);
  }
  &--wide--right img:last-child {
    width: calc(33% - 10px);
  }
  &--wide--left img:first-child {
    width: calc(33% - 10px);
  }
  &--wide--left img:last-child {
    width: calc(67% - 10px);
  }
}
